<template>
  <div>
    <div>
      <div>
        <div class="header"></div>
        <van-field
          class="readOnly"
          required
          clickable
          label=":"
          v-model="detailData.loanHead.fcName"
        />
        <van-field
          class="readOnly"
          required
          clickable
          label=":"
          v-model="detailData.loanHead.businessType == '1' ? '': ''"
        />
        <van-field
          class="readOnly"
          required
          clickable
          label=":"
          v-model="detailData.loanHead.productName"
        />
      </div>
      <div>
        <div class="header"></div>
        <van-collapse v-model="customerInfoActiveNames">
          <van-collapse-item
            v-for="(item,index) in this.detailData.customerInfoVoList"
            :key="index"
            :name="index"
          >
            <div slot="title">{{item.customerName}}</div>
            <div slot="value">{{item.customerRole}}</div>
            <van-field
              class="readOnly"
              required
              clickable
              label=":"
              v-model="item.idType == '1' ? '':''"
            />
            <van-field class="readOnly" required clickable label=":" v-model="item.idNum" />
            <van-field class="readOnly" required clickable label=":" v-model="item.phone" />
            <van-field
              class="readOnly"
              required
              clickable
              label=":"
              v-model="item.liveAddress"
            />
            <van-field class="readOnly" required clickable label=":" v-model="item.company" />
            <van-field
              class="readOnly"
              required
              clickable
              label=":"
              v-model="item.companyAddress"
            />
          </van-collapse-item>
        </van-collapse>
      </div>
      <div>
        <div class="header"></div>
        <van-collapse v-model="callPhoneActiveNames">
          <van-collapse-item
            v-for="(item,index) in this.detailData.callPhoneVoList"
            :key="index"
            :name="index"
          >
            <div slot="title">{{item.customerName}}</div>
            <div slot="value">{{item.customerType}}</div>
            <van-field
              class="readOnly"
              required
              clickable
              label=":"
              v-model="item.contactType"
            />
            <van-field
              class="readOnly"
              required
              clickable
              label=":"
              v-model="item.contactPhone"
            />
          </van-collapse-item>
        </van-collapse>
      </div>
      <div>
        <van-collapse v-model="collectionNotesActiveNames">
          <van-collapse-item name="1">
            <div class="file-item-title" slot="title">:</div>
            <div slot="value">
              <van-button style="width:80px; height:25px" type="info" @click.stop="popup"> </van-button>
            </div>
            <div class="collectionNotesBody">
              <div class="collectionNotesitem van-hairline--surround" style="line-height: 40px;">
                <div class="collectionNotesitem_left van-hairline--right"></div>
                <div class="collectionNotesitem_center van-hairline--left van-hairline--right"></div>
                <div class="collectionNotesitem_right van-hairline--left"></div>
              </div>
            </div>
            <div
              class="collectionNotesBody"
              v-for="(item, index) in detailData.collectionNotesList"
              :key="index"
            >
              <div class="collectionNotesitem van-hairline--surround">
                <div class="collectionNotesitem_left van-hairline--right">{{item.createdTime}}</div>
                <div class="collectionNotesitem_center van-hairline--right">{{item.remark}}</div>
                <div class="collectionNotesitem_right">{{item.createdBy}}</div>
              </div>
            </div>
          </van-collapse-item>
        </van-collapse>
      </div>
      <div>
        <van-collapse v-model="loanDetailActiveNames">
          <van-collapse-item name="1">
            <div class="file-item-title" slot="title">:</div>
            <van-field
              class="readOnly"
              required
              clickable
              label="():"
              v-model="detailData.loanDetail.totalAmount"
            />
            <van-field
              class="readOnly"
              required
              clickable
              label="():"
              v-model="detailData.loanDetail.term"
            />
            <van-field
              class="readOnly"
              required
              clickable
              label="():"
              v-model="detailData.loanDetail.downPaymentAmount"
            />
            <van-field
              class="readOnly"
              required
              clickable
              label="(%):"
              v-model="detailData.loanDetail.downPaymentRate"
            />
            <van-field
              class="readOnly"
              required
              clickable
              label="():"
              v-model="detailData.loanDetail.amount"
            />
            <van-field
              class="readOnly"
              required
              clickable
              label="():"
              v-model="detailData.loanDetail.netAmount"
            />
            <van-field
              class="readOnly"
              required
              clickable
              label="(%):"
              v-model="detailData.loanDetail.rate"
            />
            <van-field
              class="readOnly"
              required
              clickable
              label="():"
              v-model="detailData.loanDetail.monthRent"
            />
          </van-collapse-item>
        </van-collapse>
      </div>
      <div>
        <van-collapse v-model="loanRepaymentDetailActiveNames">
          <van-collapse-item name="1">
            <div class="file-item-title" slot="title">:</div>
            <van-field
              class="readOnly"
              required
              clickable
              label="():"
              v-model="overTotalAmount"
            />
            <van-field class="readOnly" required clickable label=":" v-model="overDays" />
            <div v-for="(item, index) in detailData.loanRepaymentDetailVoList" :key="index">
              <div style="background:#fff;height:10px"></div>

              <div style="padding:10px" class="readOnly">
                <div>
                  :
                  <span>{{getEmpty(item.term)}} </span>
                </div>
                <div style="display:flex">
                  <div style="width:50%">
                    :
                    <span>{{getDate(item.dueDate)}}</span>
                  </div>
                  <div>
                    :
                    <span>{{getEmpty(item.totalAmount)}}</span>
                  </div>
                </div>
                <div style="display:flex">
                  <div style="width:50%">
                    :
                    <span>{{getEmpty(item.capital)}}</span>
                  </div>
                  <div>
                    :
                    <span>{{getEmpty(item.interest)}}</span>
                  </div>
                </div>
                <div style="display:flex">
                  <div style="width:50%">
                    :
                    <span>{{getEmpty(item.penalty)}}</span>
                  </div>
                  <div>
                    :
                    <span>{{getEmpty(item.compound)}}</span>
                  </div>
                </div>
                <div style="display:flex">
                  <div style="width:50%">
                    :
                    <span>{{ item.repaymentDate ? getDate(item.repaymentDate) : '-'}}</span>
                  </div>
                  <div>
                    :
                    <span>{{getEmpty(item.repayment)}}</span>
                  </div>
                </div>
                <div style="display:flex">
                  <div style="width:50%">
                    :
                    <span>{{getEmpty(item.overdueAmount)}}</span>
                  </div>
                  <div>
                    :
                    <span>{{getEmpty(item.overdueDays)}}</span>
                  </div>
                </div>
              </div>
            </div>
          </van-collapse-item>
        </van-collapse>
      </div>
    </div>
    <div>
      <van-popup v-model="popupShow" position="top">
        <div>
          <div class="header"></div>
          <van-field
            required
            style="border-style: solid;border-color:#D5D5D5;border-width:1px; width:90%; margin:0 auto;margin-top:15px;"
            v-model="message"
            label=":"
            placeholder=""
            type="textarea"
            autosize
            rows="5"
            show-word-limit
          />
          <div style="margin-top:10px">
            <van-button block type="info" @click="toAdd"></van-button>
          </div>
        </div>
      </van-popup>
    </div>
  </div>
</template>

<script>
import { overdueDetail, addCollectionNotes } from "../../request/api";
import { dateFormat } from "../../utils/formatter";
import { isEmpty } from "../../utils/utils";
export default {
  data() {
    return {
      customerInfoActiveNames: [0],
      callPhoneActiveNames: [0],
      collectionNotesActiveNames: ["1"],
      loanDetailActiveNames: ["1"],
      loanRepaymentDetailActiveNames: ["1"],
      overTotalAmount: 0.0, // 
      overDays: 0,
      popupShow: false,
      message: "",
      detailData: {
        callPhoneVoList: [], //
        collectionNotesList: [], //
        customerInfoVoList: [], //
        loanDetail: {}, //
        loanHead: {}, //
        loanRepaymentDetailVoList: [] //
      }
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getEmpty(str) {
      if (isEmpty(str)) {
        return "-";
      } else {
        return str;
      }
    },
    getDate(dateStr) {
      return dateFormat(dateStr);
    },
    getData() {
      this.overDays = this.$route.params.overDays;
      overdueDetail({ loanNumber: this.$route.params.loanNumber }).then(res => {
        console.log(res.data.data);
        this.detailData = res.data.data;

        for (let index in this.detailData.loanRepaymentDetailVoList) {
          this.overTotalAmount += this.detailData.loanRepaymentDetailVoList[
            index
          ]["overdueAmount"];
        }
      });
    },
    toAdd() {
      if (this.message.length == 0) {
        this.$toast.fail("");
        return;
      }
      addCollectionNotes({
        loanNumber: this.$route.params.loanNumber,
        remark: this.message
      }).then(res => {
        this.getData();
        this.popupShow = false;
        this.message = "";
      });
    },
    popup() {
      this.popupShow = true;
    }
  }
};
</script>

<style scoped>
.collectionNotesBody {
  width: 100%;
  margin: 0 auto;
  border-style: solid;
  border-color: #d5d5d5;
  border-width: 1px;
}
.collectionNotesitem {
  display: flex;
  min-height: 40px;
  text-align: center;
}
.collectionNotesitem_left {
  width: 25%;
}
.collectionNotesitem_center {
  width: 60%;
}
.collectionNotesitem_right {
  width: 20%;
}
.file-item-title {
  color: #ff9900;
  font-size: 17px;
  font-weight: bold;
}
</style>


